.login-popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    padding: 1rem;
  }
  
  .login-popup-container {
    display: flex;
    background: white;
    border-radius: 12px;
    max-width: 900px;
    width: 100%;
    max-height: 90vh;
    overflow: hidden;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  }
  
  .login-sidebar {
    flex: 1;
    display: none;
    overflow: hidden;
  }
  
  .sidebar-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .login-content {
    flex: 1;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .login-popup-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
  }
  
  .login-popup-header h2 {
    margin: 0;
    font-size: 1.5rem;
    color: #333;
  }
  
  .close-button {
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    color: #666;
  }
  
  .login-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .form-group {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .form-group label {
    font-size: 0.9rem;
    color: #333;
  }
  
  .form-group input {
    padding: 0.75rem;
    border: 1px solid #ddd;
    border-radius: 6px;
    font-size: 1rem;
    width: 100%;
    box-sizing: border-box;
  }
  
  .form-options {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.9rem;
    margin: 0.5rem 0;
  }
  
  .remember-me {
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  
  .forgot-password {
    color: #007bff;
    text-decoration: none;
  }
  
  .forgot-password:hover {
    text-decoration: underline;
  }
  
  .login-button {
    background: #007bff;
    color: white;
    padding: 0.75rem;
    border: none;
    border-radius: 6px;
    font-size: 1rem;
    cursor: pointer;
    transition: background 0.2s;
  }
  
  .login-button:hover {
    background: #0056b3;
  }
  
  .or-divider {
    text-align: center;
    color: #666;
    position: relative;
    margin: 1rem 0;
  }
  
  .or-divider::before,
  .or-divider::after {
    content: '';
    position: absolute;
    top: 50%;
    width: 45%;
    height: 1px;
    background: #ddd;
  }
  
  .or-divider::before {
    left: 0;
  }
  
  .or-divider::after {
    right: 0;
  }
  
  .google-login-button {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    background: white;
    color: #333;
    padding: 0.75rem;
    border: 1px solid #ddd;
    border-radius: 6px;
    font-size: 1rem;
    cursor: pointer;
    transition: background 0.2s;
  }
  
  .google-login-button:hover {
    background: #f8f9fa;
  }
  
  .google-icon {
    width: 20px;
    height: 20px;
  }
  
  .signup-option {
    text-align: center;
    font-size: 0.9rem;
    color: #666;
  }
  
  .signup-option a {
    color: #007bff;
    text-decoration: none;
  }
  
  .signup-option a:hover {
    text-decoration: underline;
  }
  
  /* Responsive Design */
  @media (min-width: 768px) {
    .login-sidebar {
      display: block;
      min-width: 300px;
    }
    
    .login-content {
      padding: 3rem;
    }
    
    .login-popup-header h2 {
      font-size: 2rem;
    }
  }
  
  @media (max-width: 767px) {
    .login-popup-container {
      max-width: 100%;
      border-radius: 0;
    }
    
    .login-content {
      padding: 1.5rem;
    }
  }