/* RegisterPage.css */

/* Global Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.register-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
  margin-top: 5rem;
}

/* Register Container */
.register-container {
  display: flex;
  flex-direction: row;
  max-width: 1200px;
  margin: 2rem auto;
  flex: 1;
  gap: 2rem;
  padding: 0 1rem;
}

/* Register Card */
.register-card {
  background: white;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  flex: 1;
  max-width: 600px;
}

/* Register Header */
.register-header {
  text-align: center;
  margin-bottom: 2rem;
}

.register-header h1 {
  font-size: 1.8rem;
  color: #333;
}

.register-header p {
  font-size: 1rem;
  color: #666;
  margin-top: 0.5rem;
}

/* Registration Steps */
.registration-steps {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
}

.step {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 100px;
}

.step-number {
  width: 30px;
  height: 30px;
  background: #ccc;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  margin-bottom: 0.5rem;
}

.step.active .step-number {
  background: #007bff;
}

.step.completed .step-number {
  background: #28a745;
}

.step-label {
  font-size: 0.9rem;
  color: #666;
}

.step.active .step-label {
  color: #007bff;
}

.step-connector {
  width: 50px;
  height: 2px;
  background: #ccc;
  margin: 0 0.5rem;
}

/* Form Styles */
.register-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-step {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-size: 0.9rem;
  color: #333;
  margin-bottom: 0.5rem;
}

.form-group input {
  padding: 0.8rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  transition: border-color 0.3s;
}

.form-group input:focus {
  outline: none;
  border-color: #007bff;
}

.form-group input.error {
  border-color: #dc3545;
}

.error-message {
  color: #dc3545;
  font-size: 0.8rem;
  margin-top: 0.3rem;
}

/* Password Requirements */
.password-requirements {
  margin-top: 0.5rem;
}

.password-requirements p {
  font-size: 0.9rem;
  color: #333;
}

.password-requirements ul {
  list-style: none;
  font-size: 0.8rem;
  color: #666;
}

.password-requirements li {
  margin: 0.3rem 0;
}

.password-requirements li.met {
  color: #28a745;
}

/* Checkbox Group */
.checkbox-group {
  display: flex;
  flex-direction: column;
}

.checkbox-container {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.checkbox-container input {
  width: auto;
}

.checkbox-container label {
  font-size: 0.9rem;
  color: #333;
}

.checkbox-container label a {
  color: #007bff;
  text-decoration: none;
}

.checkbox-container label a:hover {
  text-decoration: underline;
}

/* Buttons */
.btn-register-next,
.btn-register-submit {
  background: #007bff;
  color: white;
  padding: 0.8rem;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-register-next:hover,
.btn-register-submit:hover {
  background: #0056b3;
}

.btn-register-back {
  background: #6c757d;
  color: white;
  padding: 0.8rem;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-register-back:hover {
  background: #5a6268;
}

.form-actions {
  display: flex;
  gap: 1rem;
  justify-content: space-between;
}

/* Social Register */
.social-register {
  text-align: center;
  margin-top: 1.5rem;
}

.social-register p {
  font-size: 0.9rem;
  color: #666;
  margin-bottom: 1rem;
}

.social-buttons {
  display: flex;
  gap: 1rem;
  justify-content: center;
}

.btn-social {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.8rem 1.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  background: white;
  cursor: pointer;
  font-size: 0.9rem;
}

.btn-social.google {
  color: #333;
}

/* Login Prompt */
.login-prompt {
  text-align: center;
  margin-top: 1.5rem;
}

.login-prompt p {
  font-size: 0.9rem;
  color: #666;
}

.login-prompt a {
  color: #007bff;
  text-decoration: none;
}

.login-prompt a:hover {
  text-decoration: underline;
}

/* Register Image */
.register-image {
  flex: 1;
  max-width: 600px;
  position: relative;
  display: none; /* Hidden by default on mobile */
}

.register-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
}

.image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  border-radius: 8px;
}

.image-overlay h2 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.image-overlay p {
  font-size: 1rem;
  text-align: center;
}

/* Responsive Design */

/* Tablet (768px and below) */
@media (max-width: 768px) {
  .register-container {
    flex-direction: column;
    margin: 1rem;
    gap: 1rem;
  }

  .register-card {
    max-width: 100%;
    padding: 1.5rem;
  }

  .register-header h1 {
    font-size: 1.5rem;
  }

  .registration-steps {
    flex-direction: row;
    gap: 0.5rem;
  }

  .step {
    width: 80px;
  }

  .step-connector {
    width: 30px;
  }

  .form-group input {
    font-size: 0.9rem;
    padding: 0.7rem;
  }

  .btn-register-next,
  .btn-register-submit,
  .btn-register-back {
    font-size: 0.9rem;
    padding: 0.7rem;
  }

  .social-buttons {
    flex-direction: column;
    gap: 0.5rem;
  }

  .btn-social {
    width: 100%;
    justify-content: center;
  }

  .register-image {
    display: block; /* Show image on tablet */
    max-width: 100%;
    height: 300px;
  }

  .image-overlay h2 {
    font-size: 1.3rem;
  }

  .image-overlay p {
    font-size: 0.9rem;
  }
}

/* Mobile (480px and below) */
@media (max-width: 480px) {
  .register-container {
    margin: 0.5rem;
    padding: 0 0.5rem;
  }

  .register-card {
    padding: 1rem;
  }

  .register-header h1 {
    font-size: 1.3rem;
  }

  .register-header p {
    font-size: 0.9rem;
  }

  .registration-steps {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }

  .step {
    width: 100%;
    flex-direction: row;
    gap: 0.5rem;
  }

  .step-number {
    width: 25px;
    height: 25px;
    font-size: 0.9rem;
  }

  .step-label {
    font-size: 0.8rem;
  }

  .step-connector {
    display: none; /* Hide connector on mobile */
  }

  .form-group label {
    font-size: 0.8rem;
  }

  .form-group input {
    font-size: 0.8rem;
    padding: 0.6rem;
  }

  .error-message {
    font-size: 0.7rem;
  }

  .password-requirements p,
  .password-requirements ul {
    font-size: 0.7rem;
  }

  .checkbox-container label {
    font-size: 0.8rem;
  }

  .btn-register-next,
  .btn-register-submit,
  .btn-register-back {
    font-size: 0.8rem;
    padding: 0.6rem;
  }

  .form-actions {
    flex-direction: column;
    gap: 0.5rem;
  }

  .social-register p {
    font-size: 0.8rem;
  }

  .login-prompt p {
    font-size: 0.8rem;
  }

  .register-image {
    height: 200px;
  }

  .image-overlay h2 {
    font-size: 1.1rem;
  }

  .image-overlay p {
    font-size: 0.8rem;
  }
}

/* Large Screens (1200px and above) */
@media (min-width: 1200px) {
  .register-container {
    gap: 3rem;
  }

  .register-card,
  .register-image {
    max-width: 50%;
  }

  .register-image {
    display: block;
  }

  .image-overlay h2 {
    font-size: 1.8rem;
  }

  .image-overlay p {
    font-size: 1.1rem;
  }
}